import XHeader from 'vux/src/components/x-header/index.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
  data() {
    return {
      clause: false,//去抽奖页面
      signMask: false,//签到成功弹窗
      notice: false,//签到规则弹窗
      singFlag: false,//去抽奖开关
      raffleUrl: '',//去抽奖连接
      isLoading: true,
      singValue: '0',//签到积分
      totalSign: '0',//总积分
      ReceiveCount: '',//签到攻略
      RewardCount: '',//签到攻略
      history: [],//历史签到列表
      disableDateFunction(date) {
        if (date.formatedDate === new Date().dateConversion()) return true
      },//禁止点击当天日期
      img: {
        background: 'url(' + require("../static/img/peony_main.png") + ') no-repeat',
        backgroundSize: 'cover'
      },

    }
  },
  created() {

  },
  methods: {
    goBack() {//返回
      if (this.isAndroid) {
        window.FundDetail.clickOnAndroid();
      } else if (this.isIos) {
        window.location.href = "ios://pop";
      }
    },
    sign(val) {//点击立即签到
      this.signMask=true
      if (this.singValue === '0') {
        let data = this.getQueryData();
        data.head.method = "accessPoint";
        data.info = {
          "CustomerID": this.urlData.CustomerId,//用户id
          /*"Sex": this.urlData.Sex,//性别
          "Name": decodeURI(this.urlData.Name)//用户姓名*/
        };
        this.http(data).then((data) => {
          this.singValue = data.CountIntegral;
          this.totalSign = Number(this.singValue) + Number(this.totalSign);
          this.history.push(val);
          this.singFlag = data.SignLotteryFlag === 'Y';
          this.raffleUrl = data.SignLotteryHtml;
          this.signMask = true;
        }, (e) => {
          this.$vux.alert.show({
            title: '提示',
            content: e,
          })
        })
      }
    },
    changeMonth(val, index) {//月份发送变化
      let dateTime;
      if (index !== 0) {
        if (typeof index === 'undefined') {
          dateTime = val;
          this.getSignIn();
        } else {
          if (val.month < 10) {
            val.month = '0' + val.month
          }
          dateTime = val.year + '-' + val.month;
        }
        let data = this.getQueryData();
        data.head.method = "HistoryLog";
        data.info = {
          "CustomerID": this.urlData.CustomerId,//用户id
          "DateTime": dateTime   //根据日历的时间点来查询历史记录  格式为 yyyy-MM
        };
        this.http(data).then((data) => {
          this.isLoading = false;
          this.singValue = data.Points;
          data.SignDateList.forEach((item) => {
            this.history.push(item.SignDate);
          });
        }, (e) => {
          this.isLoading = false;
          this.$vux.alert.show({
            title: '提示',
            content: e,
          })
        })
      }
    },
    getSignIn() {//获取总积分
      let data = this.getQueryData();
      data.head.action = "salesManManager";
      data.head.method = "getAccountAndIntegral";
      data.info = {
        "CustomerID": this.urlData.CustomerId,
        "UDID": this.urlData.udid,
        "UserType": this.urlData.UserType
      };
      this.http(data).then((data) => {
        this.totalSign = data.accountData.Account.Integral;
      }, (e) => {
        if (e === '没有相对应的账户信息') {
          this.singValue = '0';
          this.totalSign = '0';
        }
        this.$vux.alert.show({
          title: '提示',
          content: e,
        })
      })
    },
    getRaiders() {//获取签到攻略
      let data = this.getQueryData();
      data.head.method = "ReceiveReward";
      this.http(data).then((data) => {
        this.ReceiveCount = data.ReceiveCount;
        this.RewardCount = data.RewardCount;
      })
    },
  },
  mounted() {
    this.getRaiders();
    this.changeMonth(new Date().getYearMouth());

  },
  components: {
    XHeader, InlineCalendar, Popup, Loading
  },
}



