export default {
  install: function (Vue, options) {
    Vue.prototype.session=function (type,val) {
      if(val){
        window.sessionStorage.setItem(type,JSON.stringify(val))
      }else {
        return JSON.parse(window.sessionStorage.getItem(type))
      }
    };
    Vue.prototype.getQueryData = function () {
      return {
        "head": {
          "state": "new",
          "action": "SignAccessPoint",
          "method": ""
        },
        "info": {},
        "security": {
          "AV": "2.0",
          "DAC": "WIFI",
          "DMA": "Apple",
          "DMO": "iPhone 5 (GSM+CDMA)",
          "DOV": "10.0.1",
          "DU": "FB7F0416-FCA8-4F2F-869B-2ABFB3B28B8D",
          "Memory": "",
          "NC": "",
          "NCA": "测试电脑",
          "OS": "iOS",
          "RSL": "320*568",
          "Resolution": "",
          "USERID": "1328495419744",
          "USETIME": "1000",
          "X": "",
          "Y": "",
          "appversion": "2.0",
          "buildversion": "Ver:2.7.7_CUS_Test_160824",
          "devicemodel": "iPhone",
          "enterprise": "N",
          "id": "",
          "manufacturer": "Apple",
          "model": "iPhone",
          "name": "",
          "password": "",
          "session": "",
          "sysversion": "10.0.1",
          "udid": "FB7F0416-FCA8-4F2F-869B-2ABFB3B28B8D"
        },
      }
    };
    Date.prototype.getTargetDate = function (oF, oM, oD) {
      var _date = new Date(this);
      _date.setFullYear(_date.getFullYear() + oF);
      _date.setMonth(_date.getMonth() + oM);
      _date.setDate(_date.getDate() + oD);
      return _date;
    };
    Date.prototype.dateConversion = function () {
      return this.getFullYear() + '-' + ( (this.getMonth() + 1) < 10 ? 0 : '') + (this.getMonth() + 1) + '-' + (this.getDate() < 10 ? 0 : '') + this.getDate()
    };
    Date.prototype.getYearMouth = function () {
      return this.getFullYear() + '-' + ( (this.getMonth() + 1) < 10 ? 0 : '') + (this.getMonth() + 1)
    };

    //判断终端
    Vue.prototype.isAndroid = navigator.userAgent.indexOf('Android') !== -1;
    Vue.prototype.isIos = navigator.userAgent.indexOf('iPhone') !== -1;
    //数据请求
    Vue.prototype.http = function (data) {
      return new Promise((resolve, reject) => {
        this.$http({
          method: 'POST',
          url: process.env.NODE_ENV === 'production' ? '/MobileInsurance/H5Test' : 'http://218.107.129.235:8889/MobileInsurance/H5Test',
          //http://localhost:3000/mock
          //http://10.7.2.103:9008/MobileInsurance/H5Test
          //http://10.31.78.146:7001/MobileInsurance/H5Test
          data: data,
          timeout: 30000,
        }).then(function (data) {
          dataSolve(data.data);
        }, function (e) {
          reject('网络繁忙，请稍后再试！')
        });

        //返回结果统一处理
        function dataSolve(data) {
          if (data.Flag === 'Y') {
            resolve(data.Data);
          } else {
            reject(data.Flag.substr(2));
          }
        }
      })
    };
    //获取URL参数
    Vue.prototype.urlData = getData();
    function getData() {
      String.prototype.queryURLParameter = function () {
        let obj = {},
          reg = /([^?=&#]+)=([^?=&#]+)/g;
        this.replace(reg, function () {
          let key = arguments[1];
          let value = arguments[2];
          obj[key] = value;
        });
        return obj;
      };
      return window.location.href.queryURLParameter();
    }
  }
}






